@charset 'utf-8';
[flex-container] {
  display: flex;
  flex-direction: column;
}
[flex-container='row'] {
  display: flex;
  flex-direction: row;
}
[flex-initial] {
  flex: initial;
}
[flex-fill] {
  flex: 1;
}
#app > div,
#app main {
  display: flex;
  flex-direction: column;
  flex: 1;
}
#app #main_content {
  margin-top: 60px;
}
